import React, { useState } from "react";
import Row from 'react-bootstrap/Row';
import Form from 'react-bootstrap/Form';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import ToggleButton from 'react-bootstrap/ToggleButton';
import ToggleButtonGroup from 'react-bootstrap/ToggleButtonGroup';
import { CalendarCheck, ChevronDoubleLeft, PersonCircle, ThreeDots, CurrencyDollar } from 'react-bootstrap-icons';

const AnnotationModal = ({ show, onExited, categoryValue, toggleCategory, nameValue, toggleName }) => {

    return(
        <Modal show={show} onExit={onExited}>
        <Modal.Header>
            <Modal.Title>Select annotation attributes</Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <Form>
                <Form.Group className="mb-4" controlId="form.Category">
                    <Form.Label>Is this annotation a key entity or clause?</Form.Label>
                    <Row>
                        <ToggleButtonGroup 
                            type="radio" 
                            name="options" 
                            defaultValue="Entity"
                            value={categoryValue}
                            onChange={toggleCategory}>
                                <ToggleButton id="tbg-type-1" value="Entity" variant="outline-secondary">
                                    Entity
                                </ToggleButton>
                                <ToggleButton id="tbg-type-2" value="Clause" variant="outline-secondary">
                                    Clause
                                </ToggleButton>
                        </ToggleButtonGroup>
                    </Row>
                </Form.Group>
    
                {(categoryValue == "Entity") && <Form.Group className="mb-4" controlId="form.EntityType">
                    <Form.Label>What is the entity type?</Form.Label>
                    <Row>
                        <ToggleButtonGroup 
                            type="radio" 
                            name="options" 
                            defaultValue="Amount"
                            value={nameValue}
                            onChange={toggleName}>
                                <ToggleButton id="tbg-entity-1" value="Amount" variant="outline-secondary">
                                    <span className="entity-icon"><CurrencyDollar className="ml-4" /></span>
                                    Amount
                                </ToggleButton>
                                <ToggleButton id="tbg-entity-2" value="Parties" variant="outline-secondary">
                                    <span className="entity-icon"><PersonCircle className="ml-4" /></span>
                                    Parties
                                </ToggleButton>
                        </ToggleButtonGroup>
                    </Row>
                </Form.Group>}

                {(categoryValue == "Clause") && <Form.Group className="mb-4" controlId="form.ClauseType">
                    <Form.Label>What is the clause type?</Form.Label>
                    <Row>
                        <ToggleButtonGroup 
                            type="radio" 
                            name="options" 
                            defaultValue="Opt-out"
                            value={nameValue}
                            onChange={toggleName}>
                                <ToggleButton class="btn-outline-opt-out" id="tbg-clause-1" value="Opt-out">
                                    Opt-out
                                </ToggleButton>
                                <ToggleButton id="tbg-clause-2" value="Termination" variant="outline-secondary">
                                    Termination
                                </ToggleButton>
                                <ToggleButton id="tbg-clause-3" value="Payment" variant="outline-secondary">
                                    Payment
                                </ToggleButton>
                        </ToggleButtonGroup>
                    </Row>
                </Form.Group>}

            </Form>
        </Modal.Body>
        <Modal.Footer>
            <Button variant="primary" onClick={onExited}>
                Done
            </Button>
            {/* <Button variant="primary">Understood</Button> */}
        </Modal.Footer>
    
        </Modal>
    );
}

